import React, { useEffect, useState } from 'react'
import './style.css'
import * as backimgtop from '../../../assets/Clean Walk/Levels_Screen/BG_top.svg';
import { useHistory } from 'react-router-dom'
import { loader } from 'components/Common/core';
import Spinner from 'components/Common/Spinner';
import * as tick from '../../../assets/Clean Walk/Clues/Clue_tick.png'
import * as clue1 from '../../../assets/Clean Walk/Clues/Clue_lev_01.png'
import * as clue2 from '../../../assets/Clean Walk/Clues/Clue_lev_02.png'
import * as clue3 from '../../../assets/Clean Walk/Clues/Clue_lev_03.png'
import * as clue4 from '../../../assets/Clean Walk/Clues/Clue_lev_06.png'
import * as clue5 from '../../../assets/Clean Walk/Clues/Clue_lev_05.png'
import * as clue6 from '../../../assets/Clean Walk/Clues/Clue_lev_04.png'
import * as clue7 from '../../../assets/Clean Walk/Clues/Clue_lev_07.png'
import * as clue8 from '../../../assets/Clean Walk/Clues/Clue_lev_08.png'
import * as clue9 from '../../../assets/Clean Walk/Clues/Clue_lev_09.png'
import * as clue9locked from '../../../assets/Clean Walk/Clues/Clue_lev_09Locked.png'

import HeaderComponent from 'components/Common/HeaderComponent';

import { levelstore } from '../../../store/levelStore';

const listItems = [
  {
    "ClueLevel": "1",
    "image": clue1,
    "active": true,
  },
  {
    "ClueLevel": "2",
    "image": clue2,
    "active": true,
  },
  {
    "ClueLevel": "3",
    "image": clue3,
    "active": true,
  },
  {
    "ClueLevel": "4",
    "image": clue4,
    "active": true,
  },
  {
    "ClueLevel": "5",
    "image": clue5,
    "active": true,
  },
  {
    "ClueLevel": "6",
    "image": clue6,
    "active": true,
  },
  {
    "ClueLevel": "7",
    "image": clue7,
    "active": true,
  },
  {
    "ClueLevel": "8",
    "image": clue8,
    "active": true,
  },
  {
    "ClueLevel": "9",
    "image": clue9,
    "active": true,
  },
]


const RenderLevelScreen = () => {

  const { level, loadlevel } = levelstore();

  const [spinner, setspinner] = useState(true)
  const [list, setlist] = useState(listItems)
  const [popup, setpopup] = useState(false)
  const [alllevelcomplete, setalllevelcomplete] = useState(false)
  const history = useHistory();
  // let params = useParams();

  useEffect(() => {

    if (!level) {
      loadlevel()
    }

    if (localStorage.getItem('isFirsttime') === null) {
      loader()
    } setspinner(false)
    let a = JSON.parse(String(localStorage.getItem('EventStatusData')))
    let temp = []
    let x = {
      "ClueLevel": "9",
      "image": clue9,
      "active": true,
    }
    if (a !== null) {
      for (let i = 0; i < 8; i++) {
        // a.forEach((ele: any) => {
        if (a[i].eventType === "Task")
          listItems.forEach(ele1 => {
            if (ele1.ClueLevel === a[i].Level) {
              let abc = {
                TaskLevel: ele1.ClueLevel,
                image: ele1.image,
                active: false
              }
              if (a[i].Status === "LOCKED") {
                x.active = false
                abc.active = false
              } else {
                abc.active = true
              }
              temp.push(abc)
            }
          });
      }
      // );
    }

    // for (let i = 0; i < 8; i++) {
    //   console.log(temp[i])

    // }
    temp.push(x)

    setlist(temp)
  }, [])

  const loadicon = ((levelid: any) => {
    return (level) && level.map((obj: any) => {
      // console.log(">> title >> : ", title, obj.l_title);

      if (String(levelid) === String(obj.l_level)) {
        // console.log(">> obj.l_icon >> : ", obj.l_icon);

        return <img className={list[0].active ? "cluelist_tabimglocked" : 'cluelist_tabimg'} src={String(obj.l_icon)} />
      }
    })
  })

  return (
    <div className="back_color_main">
      <>
        <div>
          <img className='bgimgtop' src={backimgtop} />
        </div>

        <HeaderComponent />

        <div className='cluelist_container'>
          <div className='PageTitle_OrangeU'>SELECT YOUR NEXT CLUE</div>

          <div className='cluelist_gridContainer'>
            <div className='cluelist_row'>
              <div className='cluelist_col'
                onClick={() => {
                  if (list[0].active)
                    history.replace('/TaskScreen/1')
                  else
                    history.replace('/CluesScreen/1')
                }}>
                {loadicon("1")}
                {list[0].active ? <img className='cluelist_tick' src={tick} /> : null}
              </div>



              <div className='cluelist_col' onClick={() => {
                if (list[1].active)
                  history.replace('/TaskScreen/2')
                else
                  history.replace('/CluesScreen/2')

              }}>
                {loadicon("2")}
                {list[1].active ? <img className='cluelist_tick' src={tick} /> : null}
              </div>



              <div className='cluelist_col' onClick={() => {
                if (list[2].active)
                  history.replace('/TaskScreen/3')
                else
                  history.replace('/CluesScreen/3')
              }}>
                {loadicon("3")}
                {list[2].active ? <img className='cluelist_tick' src={tick} /> : null}
              </div>

            </div>

            <div className='cluelist_row'>

              <div className='cluelist_col' onClick={() => {
                if (list[3].active)
                  history.replace('/TaskScreen/4')
                else
                  history.replace('/CluesScreen/4')
              }}>
                {loadicon("4")}
                {list[3].active ? <img className='cluelist_tick' src={tick} /> : null}
              </div>

              <div className='cluelist_col'
                onClick={() => {
                  if (list[4].active)
                    history.replace('/TaskScreen/5')
                  else
                    history.replace('/CluesScreen/5')
                }}>
                {loadicon("5")}
                {list[4].active ? <img className='cluelist_tick' src={tick} /> : null}
              </div>

              <div className='cluelist_col' onClick={() => {
                if (list[5].active)
                  history.replace('/TaskScreen/6')
                else
                  history.replace('/CluesScreen/6')
              }} >
                {loadicon("6")}
                {list[5].active ? <img className='cluelist_tick' src={tick} /> : null}
              </div>
            </div>

            <div className='cluelist_row'>

              <div className='cluelist_col' onClick={() => {
                if (list[6].active)
                  history.replace('/TaskScreen/7')
                else
                  history.replace('/CluesScreen/7')
              }}>
                {loadicon("7")}
                {list[6].active ? <img className='cluelist_tick' src={tick} /> : null}
              </div>


              <div className='cluelist_col' onClick={() => {
                if (list[7].active)
                  history.replace('/TaskScreen/8')
                else
                  history.replace('/CluesScreen/8')
              }}>
                {loadicon("8")}
                {list[7].active ? <img className='cluelist_tick' src={tick} /> : null}
              </div>


              <div className='cluelist_col' onClick={() => {
                if (!list[8].active)
                  setpopup(true)
                else
                  history.replace('/CompletionScreen')
              }}>
                {loadicon("9")}
              </div>
            </div>

          </div>
        </div>

        <div className="bgbottombluebar" />

      </>

    </div >
  )
}


export default RenderLevelScreen
